"use client";

import Link from "next/link";
import { motion } from "framer-motion";
import { useState, useEffect } from "react";
import { FloatingDock } from "@/components/ui/floating-dock";
import { 
  IconHome, 
  IconInfoCircle, 
  IconBriefcase, 
  IconMail,
  IconUsers
} from "@tabler/icons-react";

const navItems = [
  { title: "Home", icon: <IconHome className="h-full w-full" style={{ color: '#FFFFFF' }} />, href: "/" },
  { title: "About", icon: <IconInfoCircle className="h-full w-full" style={{ color: '#FFFFFF' }} />, href: "/about" },
  { title: "Services", icon: <IconBriefcase className="h-full w-full" style={{ color: '#FFFFFF' }} />, href: "/services" },
  { title: "Team", icon: <IconUsers className="h-full w-full" style={{ color: '#FFFFFF' }} />, href: "/team" },
  { title: "Contact", icon: <IconMail className="h-full w-full" style={{ color: '#FFFFFF' }} />, href: "/contact" },
];

export default function Navbar() {
  const [isDark, setIsDark] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsDark(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <nav
      className="fixed top-0 left-0 right-0 z-50 transition-all duration-300"
      style={{
        backgroundColor: isDark ? "rgba(15, 23, 42, 0.95)" : "transparent",
        backdropFilter: isDark ? "blur(10px)" : "none",
      }}
    >
      <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
        {/* Logo */}
        <Link href="/">
          <motion.div
            whileHover={{ scale: 1.05 }}
            className="flex items-center gap-2"
          >
            <div
              className="w-8 h-8 rounded-md flex items-center justify-center font-bold text-sm transition-all"
              style={{
                backgroundColor: isDark ? "#10B981" : "#0F172A",
                color: "#FFFFFF",
              }}
            >
              GS
            </div>
            <span
              className="font-semibold text-lg"
              style={{ color: isDark ? "#FFFFFF" : "#0F172A" }}
            >
              Growth SuperCharged
            </span>
          </motion.div>
        </Link>

        {/* Floating Dock Navigation */}
        <FloatingDock items={navItems} />
      </div>
    </nav>
  );
}
